.profile-card-text p a{
    padding-top: 20px;
    padding-left: 0px;
    font-size: 20px;
    color: black;
    transition: 0.2s;
}

.profile-card-text p a:hover{
    color: #1eb2a6;
}

.profile-cards{
    display: flex;
    flex-direction: row;
    position: relative;
    margin-left: 15%;
    margin-top: 20%;
    justify-content: space-between;
    width: 1200px;
}

.profile-card-text{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: left;
}

.circular-card{
    border-radius: 50%;
    background-color: rgb(0,0,0,0.7);
    height: 300px;
    width: 300px;
}

.circular-card:hover{
    transition: 0.3s;
    background-color: rgb(0, 0, 0,0.3);
}